import {Route,Switch,withRouter,Redirect} from 'react-router-dom';
import React from 'react';

function RenderRoute({routes,match}){
    return <Switch>
        {
            routes.map((item,key)=>{
                let url = match.path === '/'? item.path:match.path+item.path;
                return <Route key={key} path={url} render={(props)=>{
                    let Child = item.component;
                    return <Child {...props} routes={item.children} />
                }} />
            })
        }
        <Redirect from='/' to='/login' exact/>
        <Redirect from='/layout' to='/layout/home' exact/>
    </Switch>
}

RenderRoute = withRouter(RenderRoute);//给RenderRoute加了路由信息 withRouter高阶组件
export default RenderRoute;