import React from 'react';
import {NavLink} from 'react-router-dom';
import {hash} from '../until';
import {Badge} from 'antd';
import 'antd/dist/antd.css';

class HeaderNav extends React.Component{
    render(){
        let {navList,logoSrc,username,infoNum} = this.props;
        return <div className="charge-header">
            <h1>
                <a href="###">
                    <img src={logoSrc} alt=""/>
                </a>
            </h1>
            <ul>
                {
                    navList.map((item,key)=>{
                        return <li onClick={this.handleClick.bind(this,item,key)} key={hash()}><NavLink activeClassName='active' to={item.path}>{item.text}</NavLink></li>
                    })
                }
            </ul>
            <div className="user">{username}</div>
            <div className="info">
                <Badge count={infoNum} className="num"></Badge>
                <span>通知栏</span>
            </div>
        </div>
    }
    handleClick(item,key){
        this.props.navActive(item,key);
    }
}
HeaderNav.defaultProps = {
    navList:[
        {text:'首页'},
        {text:'员工管理'},
        {text:'员工考勤'},
        {text:'工作计划'}
    ],
    navActive:(item,key)=>{
        // console.log(item,key)
    }
}
export default HeaderNav;