import React from 'react';
// import {Route,Redirect} from 'react-router-dom';
import HeaderNav from '../common/HeaderNav';
import '../css/charge.css';
import RenderRoute from '../router/renderRoute';

class Layout extends React.Component{
  constructor(props){
    super(props);
    let {path} = this.props.match;
    this.state = {
        navList:[
            {text:'首页',path:`${path}/home`},
            {text:'员工管理',path:`${path}/staff`},
            {text:'员工考勤',path:`${path}/attence`},
            {text:'工作计划',path:`${path}/workplan`},
            {text:'客户管理',path:`${path}/client`},
            {text:'亚太学院',path:`${path}/yatai`},
            {text:'级别考核',path:`${path}/check`},
            {text:'管理助手',path:`${path}/helper`},
            {text:'积分管理',path:`${path}/score`}
        ],
        logoSrc:'/img/logo.jpg',
        username:'Jim阳光',
        infoNum:3
    }
}
  render(){
    // let {path} = this.props.match;
    let {navList,logoSrc,username,infoNum} =this.state;
    let {routes} = this.props;
    return (
        <div className="charge-wrap">
          <HeaderNav navList={navList} logoSrc={logoSrc} username={username} infoNum={infoNum} navActive={this.navActive}/>
          <section>
            {/* <Route exact path={`${path}/`} component={()=><Redirect to={`${path}/home`} />}></Route>
            <Route path={`${path}/home`} component={Home}></Route>
            <Route path={`${path}/attence`} component={Attence}></Route>
            <Route path={`${path}/check`} component={Check}></Route>
            <Route path={`${path}/client`} component={Client}></Route>
            <Route path={`${path}/helper`} component={Helper}></Route>
            <Route path={`${path}/score`} component={Score}></Route>
            <Route path={`${path}/staff`} component={Staff}></Route>
            <Route path={`${path}/workplan`} component={Workplan}></Route>
            <Route path={`${path}/yatai`} component={Yatai}></Route> */}
            <RenderRoute routes={routes}/>
          </section>
        </div>
    )
    
  }
  navActive(item,key){
    console.log(item,key)
  }
}

export default Layout;