import Login from '../component/Login';
import Layout from '../component/Layout';
import Attence from '../component/attence';
import Check from '../component/check';
import Client from '../component/client';
import Helper from '../component/helper';
import Home from '../component/home';
import Score from '../component/score';
import Staff from '../component/staff';
import Workplan from '../component/workplan';
import Yatai from '../component/yatai';
import Docs from '../component/staff/docs';
import Allot from '../component/staff/allot';
import Curse from '../component/staff/Curse';
import Fee from '../component/staff/Fee';
import Birthday from '../component/staff/Birthday';
import Payment from '../component/staff/Payment';
import Leaveoff from '../component/staff/Leaveoff';


export default [
    {
        path:'/login',
        component:Login
    },
    {
        path:'/layout',
        component:Layout,
        redirect:'/home',
        children:[
            {
                path:'/attence',
                component:Attence
            },
            {
                path:'/check',
                component:Check
            },
            {
                path:'/client',
                component:Client
            },
            {
                path:'/helper',
                component:Helper
            },
            {
                path:'/home',
                component:Home
            },
            {
                path:'/score',
                component:Score
            },
            {
                path:'/staff',
                component:Staff,
                children:[
                    {
                        path:'/docs',
                        component:Docs,
                        linkText:'档案管理'
                    },
                    {
                        path:'/allot',
                        component:Allot,
                        linkText:'分配管理'
                    },
                    {
                        path:'/curse',
                        component:Curse,
                        linkText:'课程管理'
                    },
                    {
                        path:'/fee',
                        component:Fee,
                        linkText:'代课费管理'
                    },
                    {
                        path:'/birthday',
                        component:Birthday,
                        linkText:'生日提醒'
                    },
                    {
                        path:'/payment',
                        component:Payment,
                        linkText:'薪酬管理'
                    },
                    {
                        path:'/leaveoff',
                        component:Leaveoff,
                        linkText:'离职管理'
                    }
                ]
            },
            {
                path:'/workplan',
                component:Workplan
            },
            {
                path:'/yatai',
                component:Yatai
            }
        ]
    }
]