import React, { Component } from 'react';
// import {Link} from 'react-router-dom';
import RenderRoute from '../router/renderRoute';
import Content from '../common/Content';
class Staff extends Component {
    render() {
        let aside = this.props.routes.map(item=>{
            return {
                text:item.linkText,
                path:this.props.match.path + item.path
            }
        })
        
        return (
            <div className="staff">
                {/* <Link to={`${this.props.match.path}/docs`}>档案管理</Link>
                <Link to={`${this.props.match.path}/allot`}>分配管理</Link> */}
                <Content aside={aside}>
                    <div>
                        <RenderRoute routes={this.props.routes}/>
                    </div>
                </Content>
            </div>
        );
    }
}

export default Staff;